<template>
  <div class="errPage-container">
    <el-row>
      <el-col :span="10">
        <h1 class="text-jumbo text-ginormous">Oops!</h1>
        <h2>你还没有传感器</h2>
        <h6>请购买我们的产品</h6>
        <ul class="list-unstyled">
          <li>你可以去:</li>
          <li class="link-type">
            <router-link to="/dashboard">我们的主页</router-link>
          </li>
          <li class="link-type"><router-link to="/deviceManage/addDevice">添加传感器</router-link></li>
   <!--       <li><a @click.prevent="dialogVisible=true" href="#">点我看图</a></li>-->
        </ul>
      </el-col>
      <el-col :span="14">
        <img :src="errGif" width="313" height="428" alt="Girl has dropped her ice cream.">
      </el-col>
    </el-row>
    <el-dialog title="随便看" :visible.sync="dialogVisible">
      <img class="pan-img" :src="ewizardClap">
    </el-dialog>
  </div>
</template>

<script>
import errGif from '@/assets/401_images/401.gif'

export default {
  name: 'page401',
  data() {
    return {
      errGif: errGif + '?' + +new Date(),
      ewizardClap: 'https://wpimg.wallstcn.com/007ef517-bafd-4066-aae4-6883632d9646',
      dialogVisible: false,
      driver: null, // driver驱动
    }
  },
  mounted()
  {
  },
  methods: {
    back() {
      if (this.$route.query.noGoBack) {
        this.$router.push({ path: '/dashboard' })
      } else {
        this.$router.go(-1)
      }
    },
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  .errPage-container {
    width: 800px;
    margin: 100px auto;
    .pan-back-btn {
      background: #008489;
      color: #fff;
    }
    .pan-gif {
      margin: 0 auto;
      display: block;
    }
    .pan-img {
      display: block;
      margin: 0 auto;
      width: 100%;
    }
    .text-jumbo {
      font-size: 60px;
      font-weight: 700;
      color: #484848;
    }
    .list-unstyled {
      font-size: 14px;
      li {
        padding-bottom: 5px;
      }
      a {
        color: #008489;
        text-decoration: none;
        &:hover {
          text-decoration: underline;
        }
      }
    }
  }
</style>



